.container {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
  justify-content: center;
  padding: 16px; /* Add padding for better spacing */
}

.card {
  display: flex;
  flex-direction: column;
  width: 300px; /* Default width for larger screens */
  border: 1px solid #ddd;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s;
}

.card:hover {
  transform: scale(1.03); /* Add a hover effect */
}

.img img {
  width: 100%;
  height: auto;
}

.data {
  padding: 16px;
  text-align: center;
}

.star {
  color: gold;
}

.button {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
  width: 100%;
  margin-top: 8px; /* Ensure button has some spacing */
}

/* Media Queries for Responsive Design */

@media (max-width: 1200px) {
  .card {
    width: 250px; /* Adjust width for medium screens */
  }
}

@media (max-width: 992px) {
  .card {
    width: 200px; /* Adjust width for tablets */
  }
}

@media (max-width: 768px) {
  .card {
    width: 100%; /* Full width on smaller screens */
    max-width: 400px; /* Max width for better layout */
  }
}

@media (max-width: 576px) {
  .container {
    padding: 8px; /* Reduce padding on very small screens */
  }

  .card {
    width: 100%; /* Full width on mobile screens */
  }

  .data {
    padding: 12px; /* Adjust padding for smaller screens */
  }
}
