/* Container for the About section */
h1 {
  text-align: center;
  margin: 20px auto;
  font-size: 2rem;
  color: #333;
}

/* Container for content */
.contnent {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  padding: 20px;
  max-width: 1200px;
  margin: auto;
}

/* Ensure all child divs in the container have equal height */
.contnent > div {
  flex: 1;
  min-width: 300px; /* Minimum width for responsiveness */
  display: flex;
  flex-direction: column;
  justify-content: space-between; /* Distributes space evenly */
}

/* Image section styling */
.my_image {
  display: flex;
  justify-content: center;
  align-items: center;
}

.my_image img {
  max-width: 100%;
  height: auto;
  border-radius: 8px;
  border: 2px solid #ddd;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* Portfolio section styling */
.portofolio {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  background-color: #f9f9f9;
  max-width: 80%; /* Limit the width to 80% of its container */
  margin: auto; /* Center the portfolio section */
}

/* Heading styling */
.portofolio h3 {
  margin-top: 0;
  color: #333;
  font-weight: bold;
  font-size: 1.8rem;
}

/* Paragraph styling */
.portofolio p {
  margin: 10px 0;
  color: #555;
  font-size: 1rem;
}

/* Contact section styling */
.portofolio .conact {
  display: flex;
  flex-direction: column;
  gap: 10px;
  align-items: flex-start; /* Align items to the left */
}

/* Unordered list styling */
.portofolio ul {
  list-style: none;
  padding: 0;
  margin: 0;
  width: 100%; /* Full width for list items */
}

/* List item styling */
.portofolio ul li {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  color: #555;
  font-size: 1rem;
}

/* Link styling */
.portofolio ul li a {
  text-decoration: none;
  color: #555;
  font-size: 1.2rem;
  display: inline-flex;
  align-items: center;
  margin-right: 10px; /* Space between icon and text */
  transition: color 0.3s ease;
}

/* Link hover effect */
.portofolio ul li a:hover {
  color: #0056b3;
}

/* Icon styles */
.portofolio .conact a {
  font-size: 1.5rem;
}

/* Icon hover effect */
.portofolio .conact a:hover {
  color: orangered;
}

/* Responsive Design */
@media (max-width: 768px) {
  .contnent {
    flex-direction: column;
    align-items: center;
  }

  .portofolio {
    padding: 15px;
    max-width: 90%; /* Increase width constraint on smaller screens */
  }

  .portofolio .conact {
    flex-direction: column;
    align-items: flex-start; /* Maintain alignment on smaller screens */
  }

  .portofolio ul li {
    font-size: 0.9rem;
  }
}

@media (max-width: 480px) {
  .portofolio h3 {
    font-size: 1.5rem;
  }

  .portofolio p {
    font-size: 0.875rem;
  }

  .portofolio ul li {
    font-size: 0.875rem;
  }
}
