.carousel-control-prev,
.carousel-control-next {
    background-color: transparent; 
    border: none; 
    outline: none;
}

.carousel-control-prev:hover,
.carousel-control-next:hover {
    background-color: transparent; 
    box-shadow: none; 
}

/* Ensure the icons are visible */
.carousel-control-prev-icon,
.carousel-control-next-icon {
    background-color: rgba(0, 0, 0, 0.5); /* Adjust icon background if needed */
    border-radius: 50%; /* Optional: round the background */
    height: 30px; /* Adjust size as needed */
    width: 30px; /* Adjust size as needed */
}

/* Optional: Add padding or margins if needed */
.carousel-control-prev,
.carousel-control-next {
    padding: 0; /* Remove padding */
    margin: 0; /* Remove margin */
}

/* Adjust icon sizes if necessary */
.carousel-control-prev-icon,
.carousel-control-next-icon {
    background-size: 100%, 100%; /* Ensure icons fit properly */
}

.carousel-item {
    height: 500px; 
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    position: relative;
}

.carousel-item .carousel-caption {
    padding: 100px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start; /* Align items to the start */
    align-items: flex-start; /* Center horizontally */
    text-align: center; /* Center text */
    height: 100%; /* Ensure it takes full height of the carousel-item */
}

.carousel-item h1, .carousel-item p {
    margin: 0;
    padding: 10px; /* Remove default margins */
}

.carousel-item h1 {
    font-size: 50px;
    color: black;
    font-weight: bold;
}

.carousel-item p {
    font-size: 20px;
    color: rgba(0, 0, 0, 0.658);
}

.carousel-item button {
    width: 200px;
    margin-top: 10px;
    position: relative;
    padding: 10px 20px;
    border-radius: 20px;
    border: 1px solid black;
    font-size: 14px;
    text-transform: uppercase;
    font-weight: 600;
    letter-spacing: 2px;
    background: transparent;
    color: black;
    overflow: hidden;
    transition: all 0.2s ease-in;
}

.carousel-item button:hover {
    background: orangered;
    box-shadow: 0 0 30px 5px orangered;
    transition: all 0.2s ease-out;
}

.carousel-item button:hover::before {
    animation: sh02 0.5s linear;
}

.carousel-item button::before {
    content: '';
    display: block;
    width: 0;
    height: 86%;
    position: absolute;
    top: 7%;
    left: 0;
    opacity: 0;
    background: #fff;
    box-shadow: 0 0 50px 30px #fff;
    transform: skewX(-20deg);
}

@keyframes sh02 {
    from {
        opacity: 0;
        left: 0%;
    }
    50% {
        opacity: 1;
    }
    to {
        opacity: 0;
        left: 100%;
    }
}

.carousel-item button:active {
    box-shadow: 0 0 0 0 transparent;
    transition: box-shadow 0.2s ease-in;
}

.carousel-item .btn-2:hover {
    background-color: rgb(20, 83, 92);
    box-shadow: 0 0 30px 5px rgb(20, 83, 92);
    color: white;
}

.carousel-item .btn-3:hover {
    background-color: rgb(211, 22, 69);
    box-shadow: 0 0 30px 5px rgb(211, 22, 69);
    color: white;
}

.carousel-item::before {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

@media (max-width: 1200px) {
    .carousel-item {
        height: 400px;
    }
    .carousel-item .carousel-caption {
        padding: 80px;
    }
    .carousel-item h1 {
        font-size: 40px;
    }
    .carousel-item p {
        font-size: 18px;
    }
}

@media (max-width: 992px) {
    .carousel-item {
        height: 350px;
    }
    .carousel-item .carousel-caption {
        padding: 60px;
    }
    .carousel-item h1 {
        font-size: 35px;
    }
    .carousel-item p {
        font-size: 16px;
    }
    .carousel-item button {
        font-size: 12px;
    }
}

@media (max-width: 768px) {
    .carousel-item {
        height: 300px;
    }
    .carousel-item .carousel-caption {
        padding: 40px;
    }
    .carousel-item h1 {
        font-size: 30px;
    }
    .carousel-item p {
        font-size: 14px;
    }
    .carousel-item button {
        font-size: 12px;
    }
}

@media (max-width: 576px) {
    .carousel-item {
        height: 250px;
    }
    .carousel-item .carousel-caption {
        padding: 20px;
    }
    .carousel-item h1 {
        font-size: 25px;
    }
    .carousel-item p {
        font-size: 12px;
    }
    .carousel-item button {
        font-size: 10px;
    }
}
