.container {
    margin: 50px auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.dad {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 20px;
}

.wrap {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    gap: 5px ;

}

.content {
    display: flex;
    flex-direction: row;
    justify-content: center;
    border: 1px solid rgba(255, 68, 0, 0.692);
    padding: 10px;
    width: 100%;
    max-width: 400px; /* Adjust width as needed */
}

.icon {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; /* Center the icon vertically */
    padding: 10px;
}

.icon p {
    font-size: 30px;
    color: orangered;
    text-align: center; /* Center text in the icon */
}

.data {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    padding: 5px 10px;
}

.data h6, .data p {
    font-size: 18px;
}

/* Responsive Design */

/* Tablets and small desktops */
@media (max-width: 992px) {
    .content {
        flex-direction: column;
        align-items: center;
        padding: 15px;
        max-width: 100%;
    }

    .icon p {
        font-size: 28px;
    }

    .data h6, .data p {
        font-size: 16px;
    }
}

/* Mobile devices */
@media (max-width: 768px) {
    .content {
        flex-direction: column;
        align-items: center;
        padding: 10px;
        max-width: 100%;
    }

    .icon p {
        font-size: 24px;
    }

    .data h6, .data p {
        font-size: 14px;
    }

    .dad {
        flex-direction: column;
    }

    .wrap {
        justify-content: center;
    }
}

/* Small mobile devices */
@media (max-width: 576px) {
    .container {
        margin: 20px auto;
        padding: 10px;
    }

    .content {
        padding: 8px;
        max-width: 100%;
    }

    .icon p {
        font-size: 20px;
    }

    .data h6, .data p {
        font-size: 12px;
    }
}
