/* card.module.css */

/* General Styles */
.cart {
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;
  border: 1px solid #ddd;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

h2 {
  text-align: center;
  margin-bottom: 20px;
}

table {
  width: 100%;
  border-collapse: collapse;
  overflow-x: auto; /* Enable horizontal scrolling for small screens */
  display: block; /* Ensure it scrolls horizontally */
}

thead, tfoot {
  display: table;
  width: 100%;

}

tbody {
  display: table;
  width: 100%;
  overflow-x: auto; /* Allow horizontal scrolling */
}

th, td {
  text-align: center;
  width: 25%;
  padding: 10px;
  text-align: left;
  border-bottom: 1px solid #ddd;

}

th {
  background-color: orangered;
  color: white;
}

/* Image Styles */
.imageContainer {
  width: fit-content;
  height: 100px;
  overflow: hidden;
  border-radius: 4px;
}

.itemImage {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* Button Styles */
.actionButton {
  background-color: orangered;
  width: 30px;
  border: none;
  color: white;
  padding: 5px 10px;
  margin: 0 5px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
}

.actionButton:hover {
  background-color: yellow;
}

/* Footer Styles */
tfoot {
  font-weight: bold;
  background-color: #f9f9f9;
}

tfoot td {
  padding: 15px;
}

/* Responsive Styles */
@media (max-width: 768px) {
  table {
    font-size: 14px;
  }

  .imageContainer {
    width: 40px;
    height: 40px;
  }

  .actionButton {
    padding: 4px 8px;
    font-size: 14px;
  }
}

@media (max-width: 480px) {
  table {
    font-size: 12px;
  }

  thead, tfoot {
    display: none; /* Hide the table headers on small screens */
  }

  tbody {
    display: block; /* Ensure body is block to allow flexbox display */
  }

  tr {
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;
  }

  td {
    display: block;
    text-align: right;
    position: relative;
    padding: 10px 10px 10px 50%;
    font-size: 14px;
    border-bottom: 1px solid #ddd;
  }

  td::before {
    content: attr(data-label);
    position: absolute;
    left: 0;
    width: 45%;
    padding-right: 10px;
    white-space: nowrap;
    font-weight: bold;
  }
}
