/* ProductDetails.css */

/* Product details container */
.product-details-container {
    padding: 20px;
    max-width: 1200px;
    margin: 0 auto;
    text-align: center;
  }
  
  /* Heading for product details */
  .product-details-container h1 {
    font-size: 2rem;
    font-weight: bold;
    color: #333;
    margin-bottom: 20px;
  }
  
  /* Flex container for product card */
  .product-card {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  
  /* Card container */
  .card {
    border: 1px solid #ddd;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    background-color: #fff;
    margin-bottom: 20px;
    width: 100%; /* Adjust as needed */
    max-width: 600px; /* Max width for the card */
    display: flex;
    flex-direction: column; /* Stack contents vertically */
    height: 100%; /* Ensures card takes full height */
  }
  
  /* Style the product image */
  .card-img-top {
    width: 100%;
    height: 400px; /* Fixed height for the image */
    object-fit: contain; /* Ensure image is fully contained */
    border-bottom: 1px solid #ddd;
  }
  
  /* Style the card body */
  .card-body {
    padding: 20px;
    flex: 1; /* Allow card body to expand */
    display: flex;
    flex-direction: column; /* Stack contents vertically */
    align-items: center; /* Center contents horizontally */
  }
  
  /* Product title */
  .card-title {
    font-size: 1.5rem;
    font-weight: bold;
    margin-bottom: 15px;
    color: #333;
    text-align: center; /* Center the text */
    word-break: break-word; /* Break long words onto the next line */
    overflow-wrap: break-word; /* Ensure long words break within the container */
  }
  
  /* Product description */
  .card-text {
    font-size: 1.1rem;
    color: #777;
    margin-bottom: 20px;
  }
  
  /* Product price */
  .card-body p {
    font-size: 1.2rem;
    font-weight: bold;
    color: #000;
    margin-bottom: 20px;
  }
  
  /* Button style */
  .btn-primary {
    background-color: #007bff;
    border-color: #007bff;
    color: #fff;
    padding: 10px 20px;
    font-size: 1rem;
    font-weight: bold;
    border-radius: 5px;
    text-transform: uppercase;
    transition: background-color 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
  }
  
  /* Button hover state */
  .btn-primary:hover {
    background-color: #0056b3;
    border-color: #004085;
    box-shadow: 0 4px 12px rgba(0, 91, 187, 0.3);
    color: #fff;
  }
  
  /* Error message */
  .text-danger {
    color: #dc3545;
  }
  