.containerfooter {
    width: 100%;
    padding: 20px 0;
    background-color: #e59d38;
    border-top: 1px solid #e7e7e7;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.dad-1 {
    display: flex;
    justify-content: space-between;
    width: 80%;
    margin-bottom: 20px;
    flex-wrap: wrap;
}

.dad-1 .one,
.dad-1 .two,
.dad-1 .three,
.dad-1 .four,
.dad-1 .five {
    width: 18%;
    margin-bottom: 20px;
}

.dad-1 .one .logo {
    display: flex;
    align-items: center;
}

.dad-1 .one .logo img {
    height: 50px;
    margin-right: 10px;
}

.dad-1 .one .logo h4 {
    font-size: 24px;
    color: #ffffff;
    margin: 0;
}

.dad-1 .one .socail_Links {
    margin-top: 10px;
}

.dad-1 .one .socail_Links a {
    margin-right: 10px;
    font-size: 20px;
    color: #ffffff;
    transition: color 0.3s;
}

.dad-1 .one .socail_Links a:hover {
    color: #007bff;
}

.dad-1 .two h4,
.dad-1 .three h4,
.dad-1 .four h4,
.dad-1 .five h4 {
    font-size: 18px;
    margin-bottom: 15px;
    font-weight: bold;
    color: #000000;
}

.dad-1 .five li {
    color: rgb(0, 0, 0);
    font-weight: bold;
    font-size: 16px;
}

.dad-1 .two ul,
.dad-1 .three ul,
.dad-1 .four ul,
.dad-1 .five ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
}

.dad-1 .two ul li,
.dad-1 .three ul li,
.dad-1 .four ul li,
.dad-1 .five ul li {
    margin-bottom: 10px;
}

.dad-1 .two ul li a,
.dad-1 .three ul li a,
.dad-1 .four ul li a {
    color: #000000;
    text-decoration: none;
    transition: color 0.3s;
}

.dad-1 .two ul li a:hover,
.dad-1 .three ul li a:hover,
.dad-1 .four ul li a:hover {
    color: #ffffff;
}

.dad-1 .five ul li {
    display: flex;
    align-items: center;
}

.dad-1 .five ul li span {
    font-size: 20px;
    color: #ffffff;
    margin-right: 10px;
}

.dad-2 {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 80%;
    border-top: 1px solid #e7e7e7;
    padding-top: 20px;
}

.dad-2 .copyright p {
    font-size: 14px;
    color: #ffffff;
    margin: 0;
}

.dad-2 .copyright span {
    margin-right: 5px;
}

.dad-2 .payment {
    display: flex;
    flex-wrap: wrap; /* Allow wrapping if necessary */
    justify-content: center; /* Center payment icons */
    gap: 10px; /* Space between payment icons */
}

.dad-2 .payment img {
    height: 30px;
}

/* Responsive Styles */
@media (max-width: 1200px) {
    .dad-1 .one,
    .dad-1 .two,
    .dad-1 .three,
    .dad-1 .four,
    .dad-1 .five {
        width: 22%;
    }
}

@media (max-width: 992px) {
    .dad-1 {
        width: 90%;
    }

    .dad-1 .one,
    .dad-1 .two,
    .dad-1 .three,
    .dad-1 .four,
    .dad-1 .five {
        width: 100%;
        margin-bottom: 20px;
    }

    .dad-1 .one .logo {
        flex-direction: column;
        align-items: flex-start;
    }

    .dad-1 .one .logo img {
        margin-bottom: 10px;
    }

    .dad-1 .one .socail_Links {
        margin-top: 10px;
    }
}

@media (max-width: 768px) {
    .containerfooter {
        padding: 20px 10px;
    }

    .dad-1 .one,
    .dad-1 .two,
    .dad-1 .three,
    .dad-1 .four,
    .dad-1 .five {
        width: 100%;
    }

    .dad-1 .five ul li {
        font-size: 14px;
    }

    .dad-2 {
        flex-direction: column;
        align-items: center;
        text-align: center;
    }

    .dad-2 .payment {
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        margin-top: 10px;
    }

    .dad-2 .payment img {
        margin-left: 0;
        margin-right: 10px;
    }
}

@media (max-width: 576px) {
    .dad-1 .one .logo h4 {
        font-size: 20px;
    }

    .dad-1 .one .socail_Links a {
        font-size: 18px;
    }

    .dad-2 .copyright p {
        font-size: 12px;
    }
}
