
.card-container {
  display: flex;
  flex-wrap: wrap;
  gap: 20px; 
}


.card {
  border: 1px solid #ddd;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  overflow: hidden; 
  transition: transform 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
  background-color: #fff;
  margin-bottom: 20px;
  flex: 1 1 calc(20% - 20px); 
  display: flex;
  flex-direction: column; 
  height: 500px; 
}

/* Scale up the card on hover */
.card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}


.card-img-top {
  width: 100%;
  height: 200px; 
  object-fit: cover; 
  border-bottom: 1px solid #ddd;
}


.card-body {
  padding: 15px;
  text-align: center;
  flex: 1; 
  display: flex;
  flex-direction: column;
  justify-content: space-between; 
}


.card-title {
  font-size: 1.25rem;
  font-weight: bold;
  margin-bottom: 10px;
  color: #333;
  overflow: hidden; 
  text-overflow: ellipsis;
  white-space: nowrap; 
}


.card-text {
  font-size: 1rem;
  color: #777;
  margin-bottom: 15px;
  overflow: hidden; 
  white-space: nowrap; 
}


.card-body p {
  font-size: 1.1rem;
  font-weight: bold;
  color: #000;
  margin-bottom: 20px;
}


.btn-primary {
  background-color: #007bff;
  border-color: #007bff;
  color: #fff;
  padding: 10px 20px;
  font-size: 1rem;
  font-weight: bold;
  border-radius: 5px;
  text-transform: uppercase;
  transition: background-color 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
  display: inline-block;
}


.btn-primary:hover {
  background-color: #0056b3;
  border-color: #004085;
  box-shadow: 0 4px 12px rgba(0, 91, 187, 0.3);
  color: #fff;
}

.category-buttons {
  display: flex;
  flex-wrap: wrap;
  justify-content: center; 
  gap: 10px; 
  margin-bottom: 20px;
}


.category-buttons .btn {
  padding: 10px 20px;
  font-size: 1rem;
  font-weight: bold;
  border-radius: 5px;
  transition: background-color 0.3s ease, color 0.3s ease;
}


.category-buttons .btn.btn-primary {
  background-color: #007bff;
  border-color: #007bff;
  color: #fff;
}


.category-buttons .btn.btn-info {
  background-color: #f8f9fa;
  border-color: #ced4da;
  color: #495057;
}


.category-buttons .btn:hover {
  background-color: #0056b3;
  border-color: #004085;
  color: #fff;
}


@media (max-width: 768px) {
  .category-buttons .btn {
    font-size: 0.9rem;
    padding: 8px 16px;
  }
}


@media (max-width: 480px) {
  .category-buttons .btn {
    font-size: 0.8rem;
    padding: 6px 12px;
  }
}

