/* Navbar styles */
.navbar {
    position: sticky;
    top: 0;
    z-index: 1000; /* Ensure navbar is on top of other content */
    background-color: #fff; /* Background color for the navbar */
    border-bottom: 1px solid #ddd; /* Optional: Add a border at the bottom */
}

/* Make sure the container-fluid spans the full width */
.container-fluid {
    display: flex;
    align-items: center;
    justify-content: space-between; /* Align items to create space between them */
    padding: 0 20px; /* Optional: Add padding to the container */
}

/* Style for the logo and brand text */
.logo-side {
    display: flex;
    align-items: center;
}

.logo-side img {
    width: 50px;
    height: 50px;
    margin-right: 15px; /* Space between logo and text */
}

.logo-side .navbar-brand {
    font-weight: bold;
    color: #333; /* Ensure brand text color */
}

/* Style for the shopping icon */
.shopping {
    color: orangered;
    font-size: 30px;
    text-decoration: none;
    margin-right: 20px; /* Space between the shopping icon and counter */
}

/* Style for the cart counter */
.cart-counter {
    margin-left: 5px; /* Space between cart icon and counter */
    font-weight: bold;
}

/* Navbar links styling */
.navbar-nav .nav-item {
    margin-left: 15px; /* Space between navigation items */
}

.navbar-nav .nav-link {
    font-weight: bold;
    color: #333; /* Ensure nav link color */
}

.navbar-nav .nav-link:hover {
    color: orangered !important; /* Change color on hover */
}

/* Ensure responsive behavior */
@media (max-width: 768px) {
    .container-fluid {
        width: 100%;
        flex-direction: row;
        justify-content: space-between; /* Ensure space between items */
        align-items: center; /* Align items vertically center */
    }

    .logo-side {
        flex: 1; /* Allow logo-side to take up available space */
        display: flex;
        justify-content: space-between; /* Ensure space between logo and button */
        align-items: center;
    }

    .navbar-toggler {
        margin-left: 20px; /* Space between logo and button */
    }

    .navbar-nav {
        text-align: center;
        width: 100%; /* Full width for nav items */
        margin-top: 15px; /* Space above nav items */
    }

    .navbar-nav .nav-item {
        margin-left: 0;
        margin-bottom: 10px; /* Space between nav items */
    }
}
